$baseColor: #ffffff;

.auth-status {
  .header {
    margin-bottom: 0;
    h3 {
      margin-bottom: 0;
      margin-top: 5px; } }
  .project {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0; } }
  .label {
    font-size: 1em; }

  table {
      td.text-center {
        text-align: center;
        z-index: 1; }
      .ui.progress {
        border-radius: 2px;
        .bar {
          border-radius: 1px;
          &:after {
            border-radius: 1px; } } } } }


.card {
  display: inline-block;
  position: relative;
  width: 100%;
  margin: 20px 0;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
  border-radius: 3px;
  color: rgb(91, 91, 98);
  background: rgba(247, 247, 247, 0.48); }

.card [data-background-color] {
  color: #FFFFFF; }

.card [data-background-color="red"] {
  background: linear-gradient(60deg, #ef5350, #e53935);
  box-shadow: 0 12px 20px -10px rgba(244, 67, 54, 0.28), 0 4px 20px 0px rgba(0, 0, 0, 0.12), 0 7px 8px -5px rgba(244, 67, 54, 0.2); }

.card [data-background-color="green"] {
  background: linear-gradient(60deg, #66bb6a, #43a047);
  box-shadow: 0 12px 20px -10px rgba(76, 175, 80, 0.28), 0 4px 20px 0px rgba(0, 0, 0, 0.12), 0 7px 8px -5px rgba(76, 175, 80, 0.2); }

.card .card-header {
  box-shadow: 0 10px 30px -12px rgba(0, 0, 0, 0.42), 0 4px 25px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
  margin: -20px 15px 0;
  border-radius: 3px;
  padding: 15px;
  background-color: #999999; }

.card-stats .card-header {
  float: left;
  text-align: center;
  i {
    font-size: 30px; } }

.card-stats .card-content {
  text-align: right;
  padding: 15px 20px;

  .title {
    margin-top: 0; } }

.card .card-footer {
  margin: 0 20px 10px;
  padding-top: 10px;
  border-top: 1px solid #eeeeee; }

.navbar-default {
  background-color: #ffffff;
  border: none;
  box-shadow: 0 0px 5px rgba(0, 0, 0, 0.26);
  border-radius: 0; }

#inOutProgress {
  background: #e7e7e7;
  border-radius: 0; }

.ui.segments {
  border: none; }

.loading.segment {
  table {
    td {
      color: rgba(0, 0, 0, 0.26); } } }

.home-log-work-form {
  padding: 15px; }
